import React, { useState } from 'react';
import axios from 'axios';
import { useLocation, useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme();

interface IUser {
  email: string,
  firstName: string,
  lastName: string,
  phoneNumber: string,
  password: string,
}

export default  function SignUp() {

  const [ errFields, setErrFields ] = useState<IUser>({
    email: '',
    firstName: '',
    lastName: '',
    phoneNumber: '',
    password: '',
  })

  const [errStatus, setErrStatus] = useState({
    email: false,
    firstName: false,
    lastName: false,
    phoneNumber: false,
    password: false,
  })

    let navegate = useNavigate();

    const { search } = useLocation()
    const query = new URLSearchParams(search)
    let ref = query.get("Ref")
    if(!ref) {
      ref = window.localStorage.getItem("Sponsor")
    }

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const form = new FormData(event.currentTarget);
        // eslint-disable-next-line no-console
        const data = {
            email: form.get('email'),
            nombre: form.get('firstName'),
            apellidos: form.get('lastName'),
            celular: form.get('phoneNumber'),
            password: form.get('password'),
            id_ref: form.get(''),
        }

        if(!data.nombre) {
          setErrFields({ ...errFields, firstName: "Este es un campo obligatorio" })
          setErrStatus({ ...errStatus, firstName: true })
          return
        }

        if(!data.apellidos) {
          setErrFields({ ...errFields, lastName: "Este es un campo obligatorio" })
          setErrStatus({ ...errStatus, lastName: true })
          return
        }

        if(!data.celular) {
          setErrFields({ ...errFields, phoneNumber: "Este es un campo obligatorio" })
          setErrStatus({ ...errStatus, phoneNumber: true })
          return
        }

        if(!data.email) {
          setErrFields({ ...errFields, email: "Este es un campo obligatorio" })
          setErrStatus({ ...errStatus, email: true })
          return
        }

        if(!data.password) {
          setErrFields({ ...errFields, password: "Este es un campo obligatorio" })
          setErrStatus({ ...errStatus, password: true })
          return
        }

        try {
            const result = await axios.post('/api/veirify-email/', { "email": form.get('email') })
            const data = await result.data
            const idUser = data.idUser
            if(idUser > 0) {
              navegate(`/inicio`)
              alert(`Usuario ya existe con el email = ${form.get('email')}`)
              return
            }
        } catch( ex ) {
            console.log("Error: Query no permitido.  Favor ver Log del Servidor.")
        }

        try {
            data.id_ref = ref
            console.log(data)
            await axios.post('/api/user/', data)
            navegate(`/inicio`)
        } catch( ex ) {
            console.log("Error: Query no permitido.  Favor ver Log del Servidor.")
        }
    };

  const validField = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, n: number): void => {

    if(e.target.value.length > 0 && e.target.value.length < n) {
      setErrStatus({ ...errStatus, [e.target.name]: true })
      setErrFields({ ...errFields, [e.target.name]: `Campo debe tener mínimo ${n} caracteres` })
    } else {
      setErrStatus({ ...errStatus, [e.target.name]: false })
      setErrFields({ ...errFields, [e.target.name]: ""} )
    }
  }

  const validEmail = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const emailRegex = /^[-\w.%+]{1,64}@(?:[A-Z0-9-]{1,63}\.){1,125}[A-Z]{2,63}$/i;

    if (!emailRegex.test(e.target.value)) {
      setErrStatus({ ...errStatus, [e.target.name]: true })
      setErrFields({ ...errFields, [e.target.name]: `Debe indicar un nombre Email Válido.` })
    } else {
      setErrStatus({ ...errStatus, [e.target.name]: false })
      setErrFields({ ...errFields, [e.target.name]: ""} )
    }
  }

  const validPhone = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const emailRegex = /[0-9]{4}-[0-9]{4}/;

    if (!emailRegex.test(e.target.value)) {
      setErrStatus({ ...errStatus, [e.target.name]: true })
      setErrFields({ ...errFields, [e.target.name]: `Debe indicar número de célular ####-####.` })
    } else {
      setErrStatus({ ...errStatus, [e.target.name]: false })
      setErrFields({ ...errFields, [e.target.name]: ""} )
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >

          <Typography component="h1" variant="h5">
            Nuevo
          </Typography>
          <Box component="form" noValidate autoComplete="off" onSubmit={handleSubmit} sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label="Nombre"
                  autoFocus
                  autoComplete="family-name"
                  error={ errStatus.firstName }
                  onChange={ (e) => validField(e, 3) }
                  helperText={ errFields.firstName }
                  />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  name="lastName"
                  required
                  fullWidth
                  id="lastName"
                  label="Apellidos"
                  autoComplete="family-name"
                  error={ errStatus.lastName }
                  onChange={ (e) => validField(e, 3) }
                  helperText={ errFields.lastName }
                  />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="phoneNumber"
                  label="Celular"
                  name="phoneNumber"
                  error={ errStatus.phoneNumber }
                  onChange={ (e) => validPhone(e) }
                  helperText={ errFields.phoneNumber }
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email"
                  type="email"
                  name="email"
                  onChange={ (e) => validEmail(e) }
                  error={ errStatus.email }
                  helperText={ errFields.email }
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="Password"
                  error={ errStatus.password }
                  onChange={ (e) => validField(e, 6) }
                  helperText={ errFields.password }
                />
              </Grid>
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Resgistro
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link href="/inicio" >
                  Ya tienes una Cuenta? 
                  <Typography>
                    Iniciar Sesión
                </Typography>
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}